import { Box, Button, Typography, Paper } from "@mui/material";
import { useNavigate } from "react-router-dom";

function SessionExpired() {
  const navigate = useNavigate();

  const COLORS = {
    primary: "#193968",
    accent: "#cc9840",
    neutralLight: "#aeb9c7",
    neutralDark: "#5a7191",
  };

  const handleLogin = () => {
    navigate("/login-area", { replace: true });
  };

  return (
    <Box
      sx={{
        minHeight: "100vh",
        background: `linear-gradient(135deg, ${COLORS.primary}, ${COLORS.neutralDark})`,
        display: "flex",
        alignItems: "center",
        justifyContent: "center",
        px: 2,
      }}
    >
      <Paper
        elevation={10}
        sx={{
          maxWidth: 420,
          width: "100%",
          p: 4,
          borderRadius: 3,
          textAlign: "center",
          backgroundColor: "#fbfbfa",
        }}
      >
        <Box
          component="img"
          src="https://unibcompoldecom.s3.ap-south-1.amazonaws.com/uploads/1769684583111_sessionexpired.png"
          alt="Session Expired"
          sx={{
            width: "100%",
            maxWidth: 220,
            mx: "auto",
            mb: 2,
            display: "block",
          }}
        />
        <Typography
          variant="h5"
          sx={{ color: COLORS.primary, fontWeight: 700, mb: 1 }}
        >
          Session Expired
        </Typography>

        <Typography
          variant="body2"
          sx={{ color: COLORS.neutralDark, mb: 3, lineHeight: 1.6 }}
        >
          For your security, your session has expired due to inactivity. Please
          log in again to continue using the portal.
        </Typography>

        <Button
          fullWidth
          onClick={handleLogin}
          sx={{
            backgroundColor: COLORS.accent,
            color: "#ffffff",
            fontWeight: 600,
            py: 1.2,
            borderRadius: 2,
            "&:hover": {
              backgroundColor: "#b88936",
            },
          }}
        >
          Go to Login
        </Button>
      </Paper>
    </Box>
  );
}

export default SessionExpired;
